export default ()=> {
      return (
        <div className="question" data-index="115" dangerouslySetInnerHTML = {{ __html: `
<h6>115. 哪一个方法会返回 <code>'Hello world!'</code> ？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> myMap <span class="token operator">=</span> <span class="token keyword keyword-new">new</span> <span class="token class-name">Map</span><span class="token punctuation">(</span><span class="token punctuation">)</span>
<span class="token keyword keyword-const">const</span> <span class="token function-variable function">myFunc</span> <span class="token operator">=</span> <span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token operator">=></span> <span class="token string">'greeting'</span>

myMap<span class="token punctuation">.</span><span class="token function">set</span><span class="token punctuation">(</span>myFunc<span class="token punctuation">,</span> <span class="token string">'Hello world!'</span><span class="token punctuation">)</span>

<span class="token comment">//1</span>
myMap<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token string">'greeting'</span><span class="token punctuation">)</span>
<span class="token comment">//2</span>
myMap<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span>myFunc<span class="token punctuation">)</span>
<span class="token comment">//3</span>
myMap<span class="token punctuation">.</span><span class="token function">get</span><span class="token punctuation">(</span><span class="token punctuation">(</span><span class="token punctuation">)</span> <span class="token operator">=></span> <span class="token string">'greeting'</span><span class="token punctuation">)</span>
</code></pre>
<ul>
<li>A: 1</li>
<li>B: 2</li>
<li>C: 2 and 3</li>
<li>D: All of them</li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: B</h4>
<p>当通过 <code>set</code> 方法添加一个键值对，一个传递给 <code>set</code>方法的参数将会是键名，第二个参数将会是值。在这个case里，键名为 <em>函数</em> <code>() =&gt; 'greeting'</code>，值为<code>'Hello world'</code>。 <code>myMap</code> 现在就是 <code>{ () =&gt; 'greeting' =&gt; 'Hello world!' }</code>。</p>
<p>1 是错的，因为键名不是 <code>'greeting'</code> 而是 <code>() =&gt; 'greeting'</code>。
3 是错的，因为我们给<code>get</code> 方法传递了一个新的函数。对象受 <em>引用</em> 影响。函数也是对象，因此两个函数严格上并不等价，尽管他们相同：他们有两个不同的内存引用地址。</p>
</p>
</details>
` }}></div>
      );
    }